@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
  font-family: "Poppins", sans-serif;
}

.MemeHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;

  background-color: darkcyan;
  color: white;
  height: fit-content;
  padding: 10px 20px;

  box-shadow: 0 5px 5px rgba(128, 128, 128, 0.514);
}
.leftMeme {
  display: flex;
  justify-content: center;
  align-items: center;
}
.logoMeme {
  height: 7vh;
}
/* .headingMeme{} */
.rightMeme {
  display: flex;
  justify-content: center;
  align-items: center;
}
.projectHeadingMeme {
  font-weight: 500;
}
.MemeMain {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  height: fit-content;
  padding: 40px;
}

.formMeme {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: fit-content;
}

.textInput {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  width: fit-content;
  padding-top: 50px;
  padding-bottom: 20px;
}
#upperText,
#lowerText {
  margin: 10px;
  padding: 10px 60px 10px 10px;

  font-size: 1em;

  border: solid;
  border-width: 1px;
  border-color: rgba(128, 128, 128, 0.486);
  border-radius: 10px;
  box-shadow: 1px 3px 5px rgba(128, 128, 128, 0.514);
}

.meme {
  height: 40vh;
  width: 60vh;
  object-fit: contain;
  margin: 40px;
  border-radius: 20px;
  box-shadow: 1px 3px 5px rgba(128, 128, 128, 0.514);
}

.meme--div {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.buttonDiv {
  height: 50px;
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}

#submitButton {
  padding: 10px;
  width: 97%;

  background-color: darkcyan;
  color: white;

  font-weight: 300;
  font-size: 1.2em;

  border-color: transparent;
  border-radius: 10px;
  box-shadow: 1px 3px 5px rgba(128, 128, 128, 0.514);
  cursor: pointer;
}

#submitButton:hover {
  background-color: rgb(0, 156, 156);
}

#submitButton:active {
  background-color: rgb(0, 122, 122);
  box-shadow: 1px 3px 5px rgba(94, 94, 94, 0.514);
  padding: 9px;
  width: 95%;
}

.meme--text {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
    -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
    2px 2px 5px #000;
}

.bottom {
  bottom: 40px;
}

.top {
  top: 40px;
}

.meme {
  height: 50vh;
  max-width: 50vh;
  width: 100%;
  object-fit: contain;
  margin: 40px;
  border-radius: 20px;
  box-shadow: 0px 0px 5px rgba(128, 128, 128, 0.514);
}

@media screen and (max-width: 420px) {
  .MemeApp {
    min-width: none;
  }
  .MemeHeader {
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
  }

  .MemeMain {
    height: 120vh;
  }

  #upperText,
  #lowerText,
  #submitButton {
    font-size: 1.5em;
  }
}
